import React from "react"
import { graphql } from "gatsby"
import styled from "styled-components"
import posed from "react-pose"
import Logo from "../components/logo"
import { connect } from 'react-redux'
import Img from "gatsby-image"
import Div100vh from 'react-div-100vh'
import { isInfoPage, toggleIsInfoPage, isInViewport, toggleIsInViewport } from '../state/app'

import SEO from "../components/seo"


const Transition = posed.div({
  preEnter: {
    y: 1000,
    opacity: 1,
  },
  enter: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
    exit: { 
      y: 0,
      opacity: 0.5,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})

const Information = styled.div`
  background-color: ${props => props.color};
  font-family: 'Calibre-Regular';
  color: white;
  height: 100%;
  position: relative;
  z-index: -1;

  @media(min-width: 992px) {
    height: 100%;
  }
`
const Title = styled.h1`
  font-size: 48px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
`

const Text = styled.p`
  font-size: 24px;
  margin-left: 25px;
  margin-right: 25px;
  padding-top: 25px;
  max-width: 800px;
`
const InfiniteScroll = styled.div`
  height: 100vh;
  overflow-y: scroll;
`

const mapStateToProps = (state) => {
  return {
      isInfoPage: state.isInfoPage,
      isInViewport: state.isInViewport
  }
}


class Page extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      allPages: [],
      currentInViewport: null
    }

    this.refsCollection = [];
    this.infiniteScroll = null;
    this.allPagesInit = null;

    this.addElements = this.addElements.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount(){

    this.props.isInfoPage(false);
  

    let allPages = [];

    let itemsPreMain = function(data) {
      this.uid = data.uid;
      this.title = data.data.title.text;
      this.color = data.data.color;
      this.text = data.data.text.text;
      this.tags = [];
      this.images = [];
      data.tags.map(tag => {return this.tags.push(tag)});
      data.data.image_gallery.map(image => {return this.images.push(image)});
    }

    let itemsPreOthers = function(data) {
      this.uid = data.uid;
      this.title = data.data.title.text;
      this.color = data.data.color;
      this.text = data.data.text.text;
      this.tags = [];
      this.images = [];
      data.tags.map(tag => {return this.tags.push(tag)});
      data.data.image_gallery.map(image => {return this.images.push(image)});
    }

    allPages.push(new itemsPreMain(this.props.data.prismicPage));

    this.props.data.allPrismicPage.edges.map((data) => {
      return allPages.push(new itemsPreOthers(data.node));
    });

    this.setState({
      allPages: allPages
    });

    this.allPagesInit = allPages;

    this.container = this.infiniteScroll;
    
    setTimeout(()=>{
      this.refsCollection.forEach((elem) => {

        if(this.isInViewport(elem)) {
  
  
          this.props.isInViewport(elem.id);
  
        }
      })
    }, 750);

    this.isInViewport = function(elem) {
      
      var bounding = elem.getBoundingClientRect();

      if (bounding) {
        return (
          (bounding.top >= -100 &&
          bounding.top <= 0)
          ||
          (bounding.top <= -bounding.height+100 &&
          bounding.top >= -bounding.height)
      );
      }
    };
    
  }

  addElements() {
    
    this.setState(prevState => ({
      allPages: [...prevState.allPages, ...this.allPagesInit]
    }));

  }


  handleScroll() {

      if(this.container.scrollHeight - this.container.scrollTop >= this.container.clientHeight - 100 && this.container.scrollHeight - this.container.scrollTop <= this.container.clientHeight ) {
        this.addElements();
      }

      this.refsCollection.forEach((elem) => {

        if(this.isInViewport(elem)) {


          this.props.isInViewport(elem.id);


        }
      })

      if(this.refsCollection.length > 24) {
        let array = [...this.state.allPages];
        let newArray = array.splice(0, 4);

        this.setState({
          allPages: newArray
        });
      }

  }

render(){
  console.log(this.state.allPages);
  return (
    <Transition>
      <Div100vh>
      <SEO title="Projects" keywords={[`Karlanders`, `immo`, `Projects`]} />
      <Logo to={'/info'}/>
      <InfiniteScroll ref={ div => this.infiniteScroll = div} onScroll={this.handleScroll} className='infinite-scroll'>
      
      {
        this.state.allPages.map((data, index) => {
          return (
            <div ref={(div)=>{this.refsCollection[index] = div}} id={`${data.uid}`}>
            {data.images.map((image, index) => (
              <Img fluid={image.image.localFile.childImageSharp.fluid} />
                // <img key={index} src={image.image.url} />
            ))
            }
                     
          <Information color={`${data.color}`}>
            <Title>{data.title}</Title>
            <Text>{data.text}</Text>
            {/* <Tags>{data.tags}</Tags> */}
          </Information>

          </div>
            )
        })
      }
      </InfiniteScroll> 
      </Div100vh>
    </Transition>  
  )
  }
}

export default connect(
  mapStateToProps,
  dispatch => ({ isInViewport: elem => dispatch(toggleIsInViewport(elem)) ,
                 isInfoPage: operator => dispatch(toggleIsInfoPage(operator)) }),
)(Page);

export const pageQuery = graphql`
  query PostBySlug($uid: String!) {
    prismicPage(uid: { eq: $uid }) {
      tags
      uid
      data {
        title {
          text
        }
        text {
          text
        }
        color
        image_gallery {
          image {
            alt
            copyright
            url
            localFile {
              childImageSharp {
                fluid(maxWidth: 1500) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    },
    allPrismicPage(filter: { uid: {ne: $uid}}) {
      edges {
        node {
          tags
          uid
          data {
            title {
              text
            }
            text {
              text
            }
            color
            image_gallery {
              image {
                alt
                url
                localFile {
                  childImageSharp {
                    fluid(maxWidth: 1500) {
                      ...GatsbyImageSharpFluid_withWebp
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`
